body {
  margin: 0;
  padding: 0;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#cecece+0,000000+100&0.65+0,0+100 */
  background: -moz-linear-gradient(top, rgba(206, 206, 206, 0.65) 0%, rgba(0, 0, 0, 0) 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(
    top,
    rgba(206, 206, 206, 0.65) 0%,
    rgba(0, 0, 0, 0) 100%
  ); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to bottom,
    rgba(206, 206, 206, 0.65) 0%,
    rgba(0, 0, 0, 0) 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a6cecece', endColorstr='#00000000',GradientType=0 ); /* IE6-9 */
}

* {
  box-sizing: border-box;
}

.App {
  text-align: center;
  margin: 15px;
}

.Logo {
  height: 100px;
  width: 100px;
}
